<template>
  <div class="tag-list grid-h" v-if="tags">
    <d-tag
      v-for="tag in tags"
      :key="tag"
      :tag="tag"
      :selected="
        selectedTags ? selectedTags.includes(tag) : tag === selectedTag
      "
      @click.native="selectTag(tag)"
    />
  </div>
</template>

<script>
  export default {
    name: "d-tag-list",
    props: {
      tags: {
        type: Array,
        required: true,
      },
      selectedTags: {
        type: Array,
        default: () => {},
      },
      selectedTag: {
        type: String,
        default: "",
      },
    },
    data() {
      return {};
    },
    methods: {
      selectTag(tag) {
        this.$emit("onselectionchange", tag);
      },
    },
  };
</script>

<style></style>
