<template>
  <div class="card" @click="openStreak(id)">
    <div class="card__header">
      <h3 class="title">{{ title }}</h3>
    </div>
    <div class="card__body">
      <div class="text">{{ text }}</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "d-card",
    props: {
      title: {
        type: String,
        default: "",
      },
      text: {
        type: String,
        default: "",
      },
      id: {
        type: Number,
        required: true,
      },
    },
    methods: {
      openStreak(streakId) {
        this.$router.push(`/streak/${streakId}`);
      },
    },
  };
</script>

<style lang="scss" scoped>
  .card {
    background-color: var(--color-bg);
    padding: 0.6rem;

    &:hover {
      background-color: #0e0e0e;
    }

    &:active {
      transform: scale(0.99);
    }

    & .title {
      margin-bottom: 0.4rem;
      font-style: italic;
      color: var(--color-prime);
    }
  }
</style>
