import Vue from "vue";
import App from "./App.vue";
// import "./registerServiceWorker";
import router from "./router";
import store from "./store";

import arrowBack from "@/components/icons/arrowBack.vue";
import add from "@/components/icons/add.vue";
import dHeader from "@/components/dHeader.vue";
import dTag from "@/components/dTag.vue";
import dTagList from "@/components/dTagList.vue";
import dInput from "@/components/dInput.vue";
import dButton from "@/components/dButton.vue";

import wb from "./registerServiceWorker";
Vue.prototype.$workbox = wb;

Vue.config.productionTip = false;
Vue.component(dButton.name, dButton);
Vue.component(dInput.name, dInput);
Vue.component(dTagList.name, dTagList);
Vue.component(dTag.name, dTag);
Vue.component(dHeader.name, dHeader);
Vue.component(arrowBack.name, arrowBack);
Vue.component(add.name, add);

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount("#app");
